body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 175px;
  margin: 1rem;
  position: block;
  width: 175px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container > img {
  width: 100%
}
.header {
    background: rgba(0, 225, 255, 0.637);
    width: 100%;
    min-height: 80px;
    color: rgb(75, 75, 61);
    position: fixed;
    top: 0;
    margin: 0 auto;
    box-shadow: 0 5px 5px darkslategrey;
    z-index: 1000;
}

.header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.header li {
    display: inline-block;
    width: 33%;
    padding-top: 20px;
    font-family: 'Chakra Petch', sans-serif;
}

.title {
    text-align: center;
    font-family: "Arial";
}

#score {
    font-size: 1.2em;
}


@media (max-width: 700px) {

    .header li {
        display: block;
        text-align: center;
        width: 100%;
        padding-top: 0;
    } 
}
.wrapper {
  padding-top: 120px;
  background: rgba(184, 199, 209, 0.753);
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.container {
  width: 100%;
  height: 100%;
}
